	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Citizen Smash on Spain","date":"16 Mar 2004","slug":"/2004-03-16-citizen-smash-on-spain/","tags":"Y2004"},"html":"<p><a title=\"Citizen Smash - The Indepundit\" href=\"http://www.lt-smash.us/archives/002744.html#002744\">Citizen Smash:</a> \"(Some) have accused the Spanish of knuckling under to terrorists. These latter reactions are wholly inappropriate. Spain is not America, but it is an ally, and a representative democracy. It is not our place to criticize whom our allies select to be their leaders, so long as the elections are free and fair, and democracy is not undermined. The people of Spain have spoken, and the rest of us must respect their decision. Aznar had been a steadfast supporter of the United States and President Bush during a very trying time in our history. For his loyalty, he deserves our undying gratitude. Muchas gracias, Señor Aznar.\"</p>"},"next":{"fields":{"title":"CeBIT Optional","date":"16 Mar 2004","slug":"/2004-03-16-cebit-optional/"}},"previous":{"fields":{"title":"Helden","date":"17 Mar 2004","slug":"/2004-03-17-helden/"}}},{"node":{"fields":{"title":"CeBIT Optional","date":"16 Mar 2004","slug":"/2004-03-16-cebit-optional/","tags":"Y2004"},"html":"<p>Just for the record, I won't be at the <a title=\"CeBIT - Homepage Englisch\" href=\"http://www.cebit.de/homepage_e?x=1\">CeBIT</a> this year, even though <a title=\"BlogMeet at CeBIT by Heiko Hebig | hebig.com\" href=\"http://www.hebig.com/archives/001897.shtml\">several Hamburg bloggers</a> will be atttending the world's largest cattle stampede, er, I mean computer and IT trade show in Hannover (insert gratuitous Hannover joke here). Funny, back when I was not in the industry, I used to go to CeBIT on my own nickel. Nowadays, even though my company exhibits there, I have no desire to join the herd. Moo.</p>"},"next":{"fields":{"title":"Tobias is looking for a German greenish kind of liberal New Labour party","date":"15 Mar 2004","slug":"/2004-03-15-tobias-is-looking-for-a-german-greenish-kind-of-liberal-new-labour-party/"}},"previous":{"fields":{"title":"Citizen Smash on Spain","date":"16 Mar 2004","slug":"/2004-03-16-citizen-smash-on-spain/"}}},{"node":{"fields":{"title":"Tobias is looking for a German greenish kind of liberal New Labour party","date":"15 Mar 2004","slug":"/2004-03-15-tobias-is-looking-for-a-german-greenish-kind-of-liberal-new-labour-party/","tags":"Y2004"},"html":"<p><a title=\"count me in\" href=\"http://almostadiary.baldingwithgrace.de/archives/2004_03.php#001050\">Tobias is looking for a German greenish kind of liberal New Labour party</a></p>"},"next":{"fields":{"title":"Another fine moment in systems administration","date":"15 Mar 2004","slug":"/2004-03-15-another-fine-moment-in-systems-administration/"}},"previous":{"fields":{"title":"CeBIT Optional","date":"16 Mar 2004","slug":"/2004-03-16-cebit-optional/"}}}],"pathPrefix":"","first":false,"last":false,"index":509,"pageCount":1101}}

